@import 'tech';

// normalize!
@import 'normalize';
// fonts!
@import url('https://fonts.googleapis.com/css?family=Lato:100,100i,300,300i,400,400i,700,700i,900,900i|Raleway:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i&display=swap');

// colors!
$blue: #0b0237;
$lighter-blue: #1f2478;
$lavender: #6249ae;
$orange: #e37038;
$white: rgb(255, 255, 255);

// page transitions!
.transition-fade {
    transition: 0.4s;
    opacity: 1;
}

html.is-animating .transition-fade {
    opacity: 0;
    transform: translateY(40px);
}

// structure tech!
body {
    background: $blue;
    color: $white;
    box-sizing: border-box;
    margin: 0 auto;
    overflow-y: scroll;
    overflow-x: hidden;
}

* {
    font-family: 'Lato', sans-serif;
    text-decoration: none;
    // margin: 0px;

    ::-moz-selection {
        background: $orange;
    }

    ::selection {
        background: $orange;
    }
}

h1,
h2,
h3,
h4,
h5,
h5 {
    font-family: 'Raleway', sans-serif;
    text-transform: uppercase;
}

button {
    z-index: 1;
    position: relative;
    font-size: 14pt;
    font-family: inherit;
    color: white;
    padding: 0.5em 1em;
    outline: none;
    border: none;
    background-color: $lighter-blue;
    overflow: hidden;
    cursor: pointer;
    padding: 1em 2em;
    transition: ease background 190ms;

    &::after {
        content: '';
        z-index: -1;
        // background-color: hsla(0, 0%, 100%, 0.2);
        background: rgba($white, 10%);
        position: absolute;
        top: -50%;
        bottom: -50%;
        width: 2.8em;
        transform: translate3d(-525%, 0, 0) rotate(35deg);
    }

    &:hover {
        background: lighten($lighter-blue, 5%);

        &::after {
            transition: transform 0.50s ease-in-out;
            transform: translate3d(200%, 0, 0) rotate(35deg);
        }
    }
}

.container {
    width: 90vw;
    max-width: 1500px;
    margin: auto;
}

header {
    padding-top: 2em;
    position: relative;

    .header-wrapper {
        display: flex;
        flex-direction: row;
        align-items: center;
        align-content: center;
        justify-content: space-between;
        z-index: 3;
        position: relative;

        nav,
        .menu {
            text-transform: uppercase;
            font-weight: 300;
            display: grid;
            grid-auto-flow: column;
            grid-gap: 1em;

            .active {

                &::before {
                    content: "â€¢" !important;
                    position: absolute;
                    left: 50%;
                    top: -20px;
                    color: $orange;
                    // font-weight: 900;
                }
            }

            a {
                position: relative;
                text-decoration: none;
                color: $white;
                transition: color .3s ease;

                &::after {
                    --scale: 0;

                    content: '';
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 100%;
                    height: 3px;
                    background: $orange;
                    transform: scaleX(var(--scale));
                    transform-origin: var(--x) 50%;
                    transition: transform .3s cubic-bezier(.535, .05, .355, 1);
                }

                &:hover {
                    color: $orange;
                }

                &:hover::after {
                    --scale: 1;
                }
            }
        }


        svg {
            width: 200px;
            // flex-grow: 1;
        }
    }
}



section {
    h2 {
        margin-bottom: 1em;
        font-size: 24pt;
        text-align: center;

        .head_orange {
            color: $orange;
        }
    }

    hr {
        border-color: $orange;
    }
}

section+section {
    margin-top: 3em !important;
}

main {
    min-height: 600px;
}

.overview {

    position: relative;
    height: auto;
    background: $blue;

    .calltoaction {
        position: relative;
        margin-top: -7.5em;
        height: 108vh;
        z-index: 2;
        top: 0px;
        width: 100%;
        background: $lighter-blue;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: center;
        background: url('./img/bridge_v2.jpg') no-repeat;
        background-size: 100%;
        background-position: bottom left;

        // background-attachment: fixed;
        #illustration {
            background-color: $lighter-blue;
            width: 100%;
            height: auto;
            display: block;
            // overflow: hidden;
            transform: translate3d(0, 0, 0);
            text-align: center;
            opacity: 1;
            z-index: -1;
            // top:-5em;
            position: absolute;
        }

        .content-wrapper {
            // background: red;
            height: auto;
            width: 100%;
            display: flex;
            justify-content: flex-start;

            .content {
                // background: lime;
                max-width: 400px;
                padding: 2em 3em;
                border: 6px solid $white;
                background: rgba($blue, 85%);

                p {
                    margin-bottom: 1.5em;
                    line-height: 18pt;
                }
            }
        }
    }

    .main-cards {
        // padding: 3em 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: relative;
        z-index: 3;

        h2 {
            // margin-top: -2em;
        }

        .cards {
            display: inherit;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: stretch;
            justify-content: space-between;
            width: 100%;

            .card {
                flex-grow: 1;
                flex: 1 1 30%;
                min-width: 280px;
                max-width: 400px;
                min-height: 50px;
                background: linear-gradient(140deg, $lighter-blue, lighten($blue, 5%));
                // background: $lighter-blue;
                display: flex;
                flex-direction: column;
                padding: 3em 1.5em 2em 1.5em;
                overflow: hidden;
                position: relative;
                border: 4px solid $blue;
                transition: ease all 230ms;
                cursor: pointer;
                // &:nth-child(1) {
                //     background: linear-gradient(140deg, $lighter-blue, lighten($blue, 5%));
                // }

                h4 {
                    font-size: 20pt;
                    line-height: 10pt;
                    margin: 1em 0;
                    text-align: center;
                }

                p {
                    text-align: center;
                    margin: 0 2em;
                    line-height: 16pt;
                    // padding: 0 30%;
                }

                .fa-cog {
                    position: absolute;
                    font-size: 11em;
                    line-height: 0pt;
                    left: -.5em;
                    top: 10px;
                    transition: ease all 3s;
                    color: rgba($white, 10%);
                }

                .code-symbol {
                    position: absolute;
                    color: rgba($white, 10%);
                    top: -20px;
                    left: -15px;
                    font-size: 75pt;
                    font-weight: 800;
                    letter-spacing: -8pt;
                    transition: ease all 1.5s;

                }

                .bulbs {
                    transform: rotate(180deg);
                    position: absolute;
                    font-size: 8em;
                    line-height: 0pt;
                    left: 0.7em;
                    top: 1em;
                    transition: ease all 1s;
                    color: rgba($white, 10%);

                    i {
                        position: absolute;

                        &.far {
                            filter: opacity(1);
                            transition: ease all 1s;
                        }

                        &.fas {
                            filter: opacity(0);
                            transition: ease all 1s;

                        }
                    }
                }

                &:hover {
                    border-color: $orange;

                    .fa-cog {
                        transform: rotate(70deg);
                    }

                    .code-symbol {
                        letter-spacing: -10pt;
                        transform: rotate(10deg);
                    }

                    .bulbs {
                        i {
                            transform: translateY(10px);

                            &.far {
                                filter: opacity(0);
                            }

                            &.fas {
                                filter: opacity(1);

                            }
                        }
                    }
                }

                &+.card {
                    margin-left: 1em;
                }
            }
        }
    }

    .beginnings {
        position: relative;

        .about-wrapper {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            // align-items: flex-start;
            position: relative;
        }

        // .pics-wrapper {}

        .pics {
            top: 0px;
            background: $orange;
            flex: 1 30%;
            margin-right: 1em;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            max-width: 500px;
            align-self: stretch;
            background: url('./img/front.jpg') no-repeat;
            background-size: cover;
            background-position: top right;
            background-attachment: scroll;
        }

        .about {
            // margin: 0 auto;
            background: lighten($blue, 5%);
            max-width: 900px;
            padding: 3em 4em;
            flex: 1 70%;

            p {
                line-height: 18pt;

                a {
                    background: $lighter-blue;
                    padding: 3px 5px;
                    color: $orange;
                    transition: ease all 230ms;

                    &:hover {
                        color: $white;
                    }
                }

                &+p {
                    margin-top: 1.5em;
                }
            }
        }
    }

    .cool {
        position: relative;

        h2 {
            .head_actually {
                display: block;
                // margin-left: 2em;
                color: rgba($white, 25%);
            }
        }

        .cool-wrapper {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;
            // align-items: flex-start;
            position: relative;
        }

        // .pics-wrapper {
        //     position: absolute;
        //     top: 0;
        //     left: -240px;
        // }

        .pics {
            order: 2;
            top: 0px;
            background: $orange;
            flex: 1 30%;
            margin-left: 1em;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            max-width: 500px;
            align-self: stretch;
            background: url('./img/randy.jpg') no-repeat;
            background-size: cover;
            background-position: top right;
            background-attachment: scroll;
        }

        .cool {
            // margin: 0 auto;
            background: lighten($blue, 5%);
            max-width: 900px;
            padding: 3em 4em;
            flex: 1 70%;

            p {
                line-height: 18pt;

                a {
                    background: $lighter-blue;
                    padding: 3px 5px;
                    color: $orange;
                    transition: ease all 230ms;

                    &:hover {
                        color: $white;
                    }
                }

                &+p {
                    margin-top: 1.5em;
                }
            }
        }
    }

    .jobs-calltoaction {
        display: flex;
        background: lighten($blue, 5%);
        justify-content: center;
        position: relative;
        overflow: none;

        .jobs-wrapper {
            display: flex;
            justify-content: center;
            flex-direction: column;
            padding: 3em 4em;
            align-items: center;
            position: relative;
            z-index: 2;

            &:hover {
                i {
                    color: $orange;
                    transform: scale(0.8);
                }

                .fa-chevron-left {
                    transform: translateX(-30px);
                }

                .fa-chevron-right {
                    transform: translateX(30px);
                }
            }

            h2 {
                margin-bottom: 20px;
            }

            span {
                display: flex;
                flex-direction: row;
                align-items: center;

                button {
                    margin-top: 2em;

                }

                i {
                    transition: ease all 430ms;
                    font-size: 40pt;
                    margin-top: 45px;
                }

                .fa-chevron-left {
                    margin-left: 1em;
                }

                .fa-chevron-right {
                    margin-right: 1em;
                }
            }

        }
    }
}

.who-we-are {
    position: relative;
    height: auto;
    background: $blue;
    margin-top: 2em;

    .staff {
        display: flex;
        flex-direction: column;


        h1 {
            text-align: center;
            align-self: center;
        }

        .card {
            width: 250px;
            height: 280px;
            background-image: url("./img/staff/1-700x700.jpg");
            background-size: cover;
            background-position: center;
            position: relative;
        }

        .inner {
            background: red;
            padding: 1em;
            border-radius: 10px;
            width: 100%;
            height: 100%;
            position: absolute;
            clip-path: circle(10% at 90% 20%);
            transition: all .5s ease-in-out;
            cursor: pointer;

            &::before {
                height: 100%;
                width: 250px;

            }

            h1 {
                color: white;
                margin: 0;
            }

            p {
                color: white;
                font-size: .8rem;
            }

            span {
                float: right;
                color: white;
                font-weight: bold;
                transition: color .5s;
                position: relative;
                margin-right: 4%;
                top: -10px;
            }

            &:hover {
                clip-path: circle(75%);
                background: #00B6FF;

                span {
                    color: rgba(255, 255, 255, 0)
                }
            }
        }
    }
}

footer {
    background: darken($blue, 3%);
    border-top: 4px solid lighten($lighter-blue, 5%);
    margin-top: 4em !important;

    .footer-wrapper {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        padding: 2em 0;

        .logo {
            width: 100px;
            // filter: hue-rotate(180deg);
            transition: ease filter 200ms;

            &:hover {
                -webkit-filter: grayscale(100%);
                /* Safari 6.0 - 9.0 */
                filter: grayscale(100%);
                filter: brightness(4);
            }
        }

        .social {
            a {
                color: white;
                font-size: 20pt;
                transition: ease all 230ms;

                &+a {
                    margin-left: 20px;
                }

                &:hover {
                    color: $orange;
                }
            }
        }

        .address {
            font-weight: 300;
            color: rgba($white, 85%);
        }
    }
}